.game {
  margin-top: 20px;
  margin-bottom: 40px;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  &__name {
    font-size: 24px;
    margin-bottom: 10px;
    a {
      text-decoration: none;
    }
  }
  &__percentage {
    font-size: 14px;
  }
  &__user {
    font-size: 14px;
  }
  &__images {
    flex: 4;
  }
  &__description {
    flex: 1;
    font-size: 14px;
    line-height: 1.4;
    margin-top: 10px;
  }
  &__hero {
    img {
      max-width: 1024px;
    }
    margin-bottom: 10px;
  }
  &__video {
    iframe {
      width: 1024px;
      height: 400px;
    }
    margin-bottom: 10px;
  }
  &__gallery img {
    max-width: 250px;
    margin-right: 8px;
  }
  &__tags {
    margin-top: 10px;
  }
  &__tag {
    margin-right: 15px;
    font-size: 12px;
  }
}
