.app {
  padding: 10px;
  font-family: "Open Sans", sans-serif;
  &__heading {
    font-size: 32px;
    font-weight: 600;
    margin-bottom: 10px;
  }
  &__filter {
    margin-top: 15px;
  }
  &__from {
    margin-top: 15px;
    &-button {
      margin-right: 5px;
    }
  }
  &__tags {
    margin-top: 15px;
  }
  &__tag {
    font-size: 12px;
    &-button {
      border: none;
      background: none;
      color: red;
      cursor: pointer;
    }
  }
  &__count {
    margin-top: 20px;
    margin-bottom: 5px;
    font-size: 12px;
  }
  &__term {
    margin-top: 15px;
    margin-bottom: 5px;
  }
}
